}
.woocommerce-error > li > svg {
  margin-right: 10px;
}
.woocommerce-error > li::before {
  display: none;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f00d";
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
}

